.new-book-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 8px;
}

.new-book-form div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 0 auto 15px;
}

.new-book-form div > * {
  width: 100%;
  margin: 10.5px;
  padding: 5px 0;
  text-align: center;
}

.new-book-form div label {
  display: none;
}

.new-book-form div input:nth-child(4) {
  margin: 15px 22px;
}

@media screen and (max-width: 1399px) {
  .new-book-form div {
    width: 80%;
  }
}

@media screen and (max-width: 1000px) {
  .new-book-form div {
    width: 100%;
  }
}

@media screen and (max-width: 690px) {
  .new-book-form div {
    flex-direction: column;
    width: 70%;
  }

  .new-book-form div label {
    display: block;
  }
}